* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
}

main {
    padding: 36px;
}

nav {
    height: 65px;
    display: flex;
    align-items: center;
    /* Header background */
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    border: 1px solid #000000;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    color: white;
    padding: 20px;
}


.nav--logo {
    height: 100%;
    margin-right: 6px;
}


.nav--title {
    font-size: 20px;
    margin-right: auto;
}

.nav--project {
    font-size: 0.75rem;
    font-weight: 500;
}


.bold {
    font-weight: bold;
}

.gray {
    color: #918E9B;
}


section {
    padding: 20px;
}

.form {
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap:20px;
    margin-bottom: 17px;
}

.form--input {
    font-family: 'Poppins', sans-serif;
    border-radius: 5px;
    border: 1px solid #D5D4D8;
    text-indent: 5px;
}

.form--button {
    grid-column: 1 / -1;
    font-family: 'Poppins', sans-serif;
    border-radius: 5px;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    color: white;
    border: none;
    cursor: pointer;
}
.meme {
    position: relative;
}
.meme--image {
    max-width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 3px;
    position: relative;
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 20px;
}
